@import url(https://db.onlinewebfonts.com/c/64614abc4eb19976e5d29c8d3335a350?family=GothamRounded-Medium);
.main {
  background-color: #0b111d;
  display: flex;
  flex-direction: row;
  justify-content: flex;
  width: 100%;
  height: 100vh; /* This will make the background cover the entire viewport height. */
}
.borders {
  cursor: crosshair;
}

.borders * {
  cursor: crosshair;
}

.disabled {
  user-select: none;
  -webkit-user-select: none; /*Safari*/
  -moz-user-select: none; /*Firefox*/
}

.main .div {
  background: linear-gradient(
    180deg,
    rgba(39.31, 57.52, 255, 0.1) 0.29%,
    rgba(0, 0, 0, 0) 100%
  );
  user-select: none;
  -webkit-user-select: none; /*Safari*/
  -moz-user-select: none; /*Firefox*/
  background-color: #0b111d;
  border: 1px none;
  height: 1024px;
  position: relative;
  width: 1440px;
}

.main .frame {
  align-items: flex-start;
  background-color: #0b111d;
  border: 4px solid #999; /* Increase the border width and set it to white */
  border-radius: 12px; /* Increase the border radius for a larger border */
  display: flex;
  gap: 3.46px;
  left: 305px;
  overflow: hidden;
  padding: 20px; /* Adjust the padding as needed */
  position: absolute;
  top: 111px;
  width: fit-content;
}

